import React from 'react';

interface IAuthContext {
  login: (token: string) => void,
  logout: () => void,
  token?: string,
}

const AuthContext = React.createContext<IAuthContext>({
  login: () => { },
  logout: () => { },
});

const useAuth = () => React.useContext(AuthContext);

export {
  AuthContext,
  useAuth,
}

export type {
  IAuthContext,
}